import { UPDATE_STATE, REPLACE_STATE, FORCE_UPDATE } from '../shared/updateTags';
import { CALLBACK } from '../shared/effectTags';

let hasForceUpdate = false;

export function resetHasForceUpdateBeforeProcessing () {
  hasForceUpdate = false;
}

export function checkHasForceUpdateAfterProcessing () {
	return hasForceUpdate;
}

//  update 
export function createUpdate() {
	return {
		tag: UPDATE_STATE,
		payload: null,
		callback: null,
		next: null,
		nextEffect: null,
	};
}

export function processUpdateQueue(
	workInProgress,
	queue,
	props,
	instance
) {
	hasForceUpdate = false;
	
	//  updateQueue
	queue = ensureWorkInProgressQueueIsAClone(workInProgress, queue);
	
	// state  state
	let newBaseState = queue.baseState;
	let newFirstUpdate = null;

	let update = queue.firstUpdate;
	let resultState = newBaseState;

	while (update !== null) {
		resultState = getStateFromUpdate(
			workInProgress,
			queue,
			update,
			resultState,
			props,
			instance,
		);
		
		const callback = update.callback;

		if (callback !== null) {
			workInProgress.effectTag |= CALLBACK;

			// update nextEffect 
			update.nextEffect = null;

			if (queue.lastEffect === null) {
				//  lastEffect 
				queue.firstEffect = queue.lastEffect = update;
			} else {
				queue.lastEffect.nextEffect = update;
				queue.lastEffect = update;
			}
		}

		//  update
		update = update.next;
	}
	
	//  updateQueue update l astUpdate  null
	queue.firstUpdate = null;
	queue.lastUpdate = null;

	queue.baseState = newBaseState; //  state

	//  state
	workInProgress.memoizedState = resultState;
}

export function enqueueUpdate(fiber, update) {
	const alternate = fiber.alternate;
	
	let firstQueue;
	let secondQueue;
	if (alternate === null) {
		// updateQueueupdateQueue
		firstQueue = fiber.updateQueue;
		secondQueue = null;
		if (firstQueue === null) {
			fiber.updateQueue = createUpdateQueue(fiber.memoizedState);
			firstQueue = fiber.updateQueue;
		}
	} else {
		
		firstQueue = fiber.updateQueue;
		secondQueue = alternate.updateQueue;
		// commit work fiberupdateQueuenull
		if (firstQueue === null) {
			if (secondQueue === null) {
				firstQueue = fiber.updateQueue = createUpdateQueue(
					fiber.memoizedState,
				);
				secondQueue = alternate.updateQueue = createUpdateQueue(
					alternate.memoizedState,
				);
			} else {
				//  q2  q1 current
				firstQueue = fiber.updateQueue = cloneUpdateQueue(secondQueue);
			}
		} else {
			if (secondQueue === null) {
				secondQueue = alternate.updateQueue = cloneUpdateQueue(firstQueue);
			} else {
				// currentalternate  updateQueue
			}
		}
	}

	//  update
	if (secondQueue === null || firstQueue === secondQueue) {
		appendUpdateToQueue(firstQueue, update);
	} else {
		//  
		if (firstQueue.lastUpdate === null || secondQueue.lastUpdate === null) {
			appendUpdateToQueue(firstQueue, update);
			appendUpdateToQueue(secondQueue, update);
		} else {
			//    lastUpdateupdate
			appendUpdateToQueue(firstQueue, update);
			secondQueue.lastUpdate = update;
		}
	}
}

function getStateFromUpdate(
	workInProgress,
	queue,
	update,
	prevState, // resultState
	nextProps, // props
	instance,
) {
	switch (update.tag) {
		case REPLACE_STATE: {
			const payload = update.payload;
			//  this.setState((state, props) => {}) 
			if (typeof payload === 'function') {
				return payload.call(instance, prevState, nextProps);
			}
			
			// REPLACE_STATE 
			return payload;
		}
		
		case UPDATE_STATE: {
			const payload = update.payload;
			let partialState;

			//  this.setState((state, props) => {}) 
			if (typeof payload === 'function') {
				partialState = payload.call(instance, prevState, nextProps);
			} else {
				partialState = payload;
			}

			// 
			if (partialState === null || partialState === undefined) {
				return prevState;
			}

			//  state
			return {
				...prevState, 
				...partialState
			}
		}

		case FORCE_UPDATE: {
			hasForceUpdate = true;
			return prevState;
		}
	}

	return prevState;
}

// updateQueue 
function createUpdateQueue(baseState) {
	return {
		baseState,
		firstUpdate: null,
		lastUpdate: null,
		firstEffect: null,
		lastEffect: null,
	};
}


function appendUpdateToQueue(queue, update) {
	const lastUpdate = queue.lastUpdate;
	// lastUpdate update
	// updatenextlastUpdate
	if (lastUpdate === null) {
		queue.firstUpdate = queue.lastUpdate = update;
	} else {
		queue.lastUpdate.next = update;
		queue.lastUpdate = update;
	}
}

function cloneUpdateQueue(queue) {
	 return {
		baseState: queue.baseState,
		firstUpdate: queue.firstUpdate,
		lastUpdate: queue.lastUpdate,

		firstEffect: null,
		lastEffect: null,
	};
}

function ensureWorkInProgressQueueIsAClone(workInProgress, queue) {
	const current = workInProgress.alternate;

	// workInProgress updateQueue updateQueue
	if (current !== null) {
		if (queue === current.updateQueue) {
			queue = workInProgress.updateQueue = cloneUpdateQueue(queue);
		}
	}

	return queue;
}
